import "core-js/modules/es.array.push.js";
import { ref } from 'vue';
import axios from 'axios';
export default {
  data() {
    return {
      loginForm: {
        username: 'admin',
        password: '123456',
        role: ''
      },
      loginFormRules: {
        username: [{
          required: true,
          message: '用户名不能为空',
          trigger: 'blur'
        }, {
          min: 3,
          max: 10,
          message: '用户名长度为3-10个字符',
          trigger: 'blur'
        }],
        password: [{
          required: true,
          message: '密码不能为空',
          trigger: 'blur'
        }, {
          min: 6,
          max: 15,
          message: '密码长度为6-15个字符',
          trigger: 'blur'
        }]
      }
    };
  },
  methods: {
    login_a() {
      this.$refs.loginformref.validate(a => {
        console.log(a);
        if (!a) return;
        const res1 = this.loginForm;
        console.log(res1);
        axios.post('http://127.0.0.1:2333/api/login', res1).then(response => {
          console.log('Response:', response);
          if (this.loginForm.role == 'tutor') {
            this.$router.push('/tutors');
          } else {
            this.$router.push('/student');
          }
        }).catch(error => {
          console.error('Error:', error);

          // 处理错误
        });
      });
    },
    register() {
      this.$router.push('/register');
    }
  }
};